import React, { useEffect } from "react";
import { AppStoreLink, GooglePlayLink } from "../utils/app-stores";
import { getMobileOperatingSystem } from "../utils/utils";

export interface AppStoreComponentProps {}

const AppStoreComponent = () => {
  useEffect(() => {
    // @ts-ignore
    const md = getMobileOperatingSystem();
    if (md === "ios") {
      // @ts-ignore
      window.location = AppStoreLink;
    } else {
              // @ts-ignore

      window.location = GooglePlayLink;
    }
  }, []);

  return <div />;
};
export default AppStoreComponent;
